<template>
  <div>
    <div class="tile is-ancestor">
      <div class="tile is-parent">
        <article class="tile is-child box">
          <p class="subtitle">FIND TRANSACTION</p>
          <hr/>
          <div class="columns">
            <div class="column">
              <div class="control">
              <label class="label">Date</label>
              <datepicker :inputClass="{ test: true }" :config="{ wrap: true }" v-model="value.date">
                <a class="button" data-toggle><i class="fa fa-calendar"></i></a>
              </datepicker>
              </div>
            </div>
            <div class="column">
              <div class="control">
              <label class="label">MSISDN</label>
              <input class="input" type="text" v-model="value.msisdn" placeholder="MSISDN Number"></input>
              </div>
            </div>
            <div class="column">
              <div class="control">
                <label class="label"></label>
        </br>
                <a class="button is-dark" @click="loadData">Show</a>
              </div>
            </div>
          </div>
          <div class="field is-grouped">
          </div>
          <div class="field">
          </div>
        </article>
      </div>
    </div>
   <div class="tile is-ancestor">
      <div class="tile is-parent">
        <article class="tile is-child box">
          <h4 class="subtitle">RAW DATA SMS</h4>
          <div class="table-responsive">
            <table class="table is-bordered is-striped is-narrow">
              <thead>
                <tr>
                  <th>TrxID</th>
                  <th>Destination</th>
                  <th>Message</th>
                  <th>Time</th>
                  <th>Status</th>
                </tr>
              </thead>
              <!-- <tfoot>
                <tr>
                  <th>TrxID</th>
                  <th>Destination</th>
                  <th>Message</th>
                  <th>Time</th>
                  <th>Status</th>
                </tr>
              </tfoot> -->
              <tbody>
              <tr v-for="item in items">
                <td>{{ item.trxid }}</td>
                <td>{{ item.id }}</td>
                <td>{{ item.message }}</td>
                <td>{{ item.date | formatDate }}</td>
                <td>{{ item.status }}</td>
              </tr>
              </tbody>
            </table>
          </div>
        </article>
      </div>
    </div>
  </div>
</template>

<script>
  import moment from 'moment'
  import Datepicker from 'vue-bulma-datepicker'
  import Chart from 'vue-bulma-chartjs'

  export default {
    components: {
      Datepicker,
      Chart
    },
    data () {
      return {
        items: [{
          trxid: null,
          date: null,
          trxtype: null,
          id: null,
          message: null,
          product: null,
          status: null,
          userid: null,
          retailer: null
        }],
        value: {
          date: null,
          msisdn: null
        }
      }
    },

    computed: {
      today () {
        return moment().format('YYYY-MM-DD hh:mm:ss')
      }
    },
    methods: {
      loadData () {
        this.$http({
          url: 'http://5.189.176.218:6680/rawdata',
          method: 'post',
          data: {
            startDate: this.value.date,
            msisdn: this.value.msisdn
          }
        }).then((response) => {
          this.items = response.data.data
          // this.items.date = moment(response.data.data.date).format('YYYY-MM-DD HH:mm:ss')
          // this.items.trxid = response.data.data.trxid
          // this.items.id = response.data.data.id
          // this.items.message = response.data.data.message
          // this.items.status = response.data.data.status
        }).catch((error) => {
          console.log(error)
        })
      }
    },

    mounted () {
      let hari = new Date()
      let yyyy = hari.getFullYear().toString()
      let mm = (hari.getMonth() + 1).toString()
      let dd = hari.getDate().toString()

      let mmChars = mm.split('')
      let ddChars = dd.split('')

      this.value.date = yyyy + '-' + (mmChars[1] ? mm : '0' + mmChars[0]) + '-' + (ddChars[1] ? dd : '0' + ddChars[0])
    }
  }
</script>

<style lang="scss">
.table-responsive {
  display: block;
  width: 100%;
  min-height: .01%;
  overflow-x: auto;
}
</style>



